.movieGrid {
  color: #fff;
  margin-top: 4rem;
  text-align: center;
}

.movieGrid__carousel {
  display: flex !important;
  align-items: center !important;
  justify-content: space-between;
  padding-bottom: 10px !important;
}

.movieGrid__carousel > .MuiSvgIcon-root {
  border: 1px solid #fff;
  padding: 3px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 2rem;
  transition: all 0.1s ease-in;
}

.movieGrid__carousel > .MuiSvgIcon-root:hover {
  /* border-color: #222; */
  box-shadow: 2px 2px 5px #888;
}

.movieGrid__carousel > h1 {
  font-family: Oswald, sans-serif;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.movieGrid__votes {
  /* font-size: 1.2rem; */
  font-family: Manrope, sans-serif;
  font-weight: 500;
  margin-bottom: 3rem;
  color: #fff;
}

.movieGrid__votes > span {
  color: #fff;
  font-size: 1.3rem;
  font-weight: 700;
}

.movieGrid > a {
  color: #bbb;
  text-decoration: underline;
  text-transform: uppercase;
  font-size: medium;
  display: inline-block;
  padding-bottom: 2rem;
}

.movieGrid__carousel {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3,0.2fr);
  place-items: center;
  max-width: 76.5%;
  grid-row-gap: 12px;
  grid-column-gap: 12px;
  margin-top: 1rem;
  padding-bottom: 50px;
}

.movieGrid__container{
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3,0.2fr);
  place-items: center;
  max-width: 64%;
  grid-row-gap: 12px;
  grid-column-gap: 12px;
  margin-top: 1rem;
  padding-bottom: 50px;
}

.movieGrid__movies {
  display: flex;
  width: 330px;
  height: 120px;
  background-color: #050505ad;
  position: relative
}

.movieGrid__votedOnce {
  opacity: 0.5;
}

.movieGrid__votedOnce:first-of-type {
  opacity: 1;
}

.movieGrid__moviesBtn0 {
  background-color: #111 !important;
}

.movieGrid__movies > img {
  width: 150px;
  object-fit: contain;
}
.movieGrid__movies > svg {
  position: absolute;
  bottom: 5px;
  left:115px;
}

.movieGrid__movies > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  width: 100%;
  text-align: center;
}

.movieGrid__movies > div > h2 {
  text-transform: uppercase;
  font-size: small;
  font-family: Oswald, sans-serif;
  font-weight: 600;
  letter-spacing: 0.3px;
}

.movieGrid__moviesBtn {
  /* background-color: white; */
  background: #1C1C41;
  color: #fff;
  padding: 5px 20px;
  text-transform: uppercase;
  font-family: Oswald, sans-serif;
  /* font-weight: 500; */
  font-size: 10px;
  letter-spacing: 0.7px;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.2s ease-in;
  outline: none;
  border: none;
  margin: 4px;
  /* color: black; */
}

.movieGrid__moviesBtn:disabled {
  cursor: default;
}

.movieGrid__moviesBtn:disabled:hover {
  background: #000;
}

.movieGrid__moviesBtn:hover {
  /* background-color: #222;
  color: white; */
  box-shadow: 1px 1px 1px #888;
  background: linear-gradient(to right, #1C1C41, #555);
}

/* MODAL */
.MuiPaper-root {
  background-color: transparent !important;
}

.movieGrid__modal {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #222;
  height: 150px;
  width: 270px;
  border-radius: 10px;
}

.movieGrid__modal > h1 {
  font-size: 1.7rem;
}

.movieGrid__modal > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
}


.movieGrid__modal > div > button {
  outline: none;
  border: none;
  padding: 12px 30px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.movieGrid__modal3 {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items:flex-start;
  justify-content: space-around;
  background-color: #222;
  height: 150px;
  width: 270px;
  border-radius: 10px;
  padding: 20px;
}

.movieGrid__modal3 > h1 {
  font-size: 1.7rem;
}

.movieGrid__modal3 > div {
  align-items: center;
  justify-content: space-between;
  width: 90%;
}

.movieGrid__modal3 > div > button {
  outline: none;
  border: none;
  padding: 12px 30px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in;
}



.movieGrid__modal > div > button:hover {
  filter: brightness(150%);
}

.movieGrid__modal > div > button:first-of-type {
  background-color: #444;
  color: #fff;
}

.movieGrid__modal > div > button:last-of-type {
  background-color: #228b22;
}

/* SECOND MODAL */
.movieGrid__modalSecond > h1 {
  font-size: 1.5rem;
  font-weight: 700;
}

.movieGrid__modalSecond > h1 > span {
  color: #fff;
}

.movieGrid__modalSecond > button {
  outline: none;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #444;
  color: #fff;
  cursor: pointer;
}

/* EXPIRED GRID */
.expiredGrid__moviesBtn {
  cursor: default;
}

.expiredGrid__moviesBtn:hover {
  background-color: #000;
}

@media screen and (max-width:768px){
  .movieGrid__container{
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1,0fr);
    place-items: center;
    max-width: 90%;
    grid-row-gap: 12px;
    grid-column-gap: 12px;
    margin-top: 1rem;
    padding-bottom: 50px;
  }

}

.sc-user-input {
  display: none!important;
  visibility: 0  !important;
}
.sc-chat-window{
z-index: 1000 !important;
}

.sc-launcher{
  cursor: pointer !important;
}

.sc-message-list{
background-color:#000000ad !important
}

.sc-message--avatar{
  visibility:hidden
}

.sc-message--content.received .sc-message--text{
  margin-right:0px !important;
  text-align:left !important
}

.sc-message--avatar{
  margin-right:0px !important
}

textarea {
  width: 250px;
  height: 50px;
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  background: none repeat scroll 0 0 #FFFFFF;
  border-color: -moz-use-text-color #FFFFFF #FFFFFF -moz-use-text-color;
  border-image: none;
  border-radius: 6px 6px 6px 6px;
  border-style: none solid solid none;
  border-width: medium 1px 1px medium;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.12) inset;
  color: #555555;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 1em;
  line-height: 1.4em;
}


textarea:focus {

    outline-width: 0;
}